<script>
  import { createEventDispatcher } from "svelte";
  import { actionqueue } from "./addon_action_queue.js";
  const dispatch = createEventDispatcher();
  import { permissions } from "./store_permissions.js";
  import { deepmerge } from "../merge.js";

  export let addon = { permissions: { mandatory: [], optional: [] } };
  export let userdata = { installations: [] };

  let queue = {};
  let error_message = null;

  actionqueue.subscribe(i => {
    if (i[addon.id]) {
      queue = i[addon.id];
    }
  });

  $: installations = userdata.installations
    ? Object.values(userdata.installations).map(item => {
        let copy = Object.assign(
          { thisaddon: item.addons[addon.id] || {} },
          item
        );
        if (!copy.thisaddon.s) copy.thisaddon.s = null;
        clear_queued_if_state_diff(copy);
        return copy;
      })
    : [];

  $: perm = addon.permissions.mandatory
    .map(key => Object.assign({ mandatory: true }, $permissions[key]))
    .concat(
      addon.permissions.optional.map(key =>
        Object.assign({ mandatory: false }, $permissions[key])
      )
    );

  function confirm_queued(installation) {
    actionqueue.update(e => {
      if (e[addon.id] === undefined) e[addon.id] = {};
      e[addon.id][installation.id] = {
        code: "queue",
        s: installation.thisaddon.s
      };
      return e;
    });
  }

  function is_old_version(install_addon) {
    console.log("IS_CURRENT", install_addon.v, addon.version);
    try {
      const v1 = install_addon.v;
      const v2 = addon.version;
      v1 = v1.split(".");
      v2 = v2.split(".");
      const k = Math.min(v1.length, v2.length);
      for (let i = 0; i < k; ++i) {
        v1[i] = parseInt(v1[i], 10);
        v2[i] = parseInt(v2[i], 10);
        if (v1[i] > v2[i]) return 1;
        if (v1[i] < v2[i]) return -1;
      }
      //return v1.length == v2.length ? 0 : v1.length < v2.length ? -1 : 1;
      if (v1.length < v2.length);
    } catch (_) {
      return false;
    }
  }

  /**
   * Remove a queued command, when the given state ("installed" etc) differs from the queued state
   */
  function clear_queued_if_state_diff(installation) {
    if (
      $actionqueue[addon.id] &&
      $actionqueue[addon.id][installation.id] &&
      $actionqueue[addon.id][installation.id].s == thisaddon.s
    )
      actionqueue.update(e => {
        delete e[addon.id][installation.id];
        return e;
      });
  }

  function clear_queued(installation, err = null) {
    if (err) error_message = err.message;
    if ($actionqueue[addon.id] && $actionqueue[addon.id][installation.id])
      actionqueue.update(e => {
        delete e[addon.id][installation.id];
        return e;
      });
  }

  function action(e, installation, installation_index, code) {
    error_message = null;
    actionqueue.update(e => {
      if (e[addon.id] === undefined) e[addon.id] = {};
      e[addon.id][installation.id] = {
        code: "push",
        s: installation.thisaddon.s
      };
      return e;
    });
    dispatch("confirm", {
      installation: installation.id,
      id: addon.id,
      code,
      confirm: () => confirm_queued(installation),
      error: err => clear_queued(installation, err)
    });
  }

  function cap_description(cap) {
    switch (cap) {
      case "CHOWN":
      case "DAC_OVERRIDE":
      case "DAC_READ_SEARCH":
      case "FOWNER":
      case "FSETID":
        return "<span class='text-danger'>Bypass any process permission checks</span>";
      case "BLOCK_SUSPEND":
      case "SYS_BOOT":
      case "CAP_WAKE_ALARM":
        return "Allows to block system suspend, reboot and set wakeup timers.";
      case "NET_ADMIN":
        return "<span class='text-danger'>Full access to the host networking, including potential traffic sniffing and man in the middle attacks</span>";
      case "NET_BIND_SERVICE":
        return "Allows to bind to a privileged port (port number less than 1024)";
      case "NET_RAW":
        return "Allows to use RAW and PACKET sockets. Allows transparent proxing. <span class='text-danger'>Might be abused for traffic sniffing and man in the middle attacks.</span>";
      case "SYS_ADMIN":
      case "SYS_MODULE":
      case "SYS_RAWIO":
      case "SYS_PTRACE":
        return "<span class='text-danger'>Full system access, loading of arbitrary kernel modules, access other processes memory!</span>";
      case "SYS_TIME":
        return "Allows to set the system clock. Might be abused to attack time based encryption.";
      case "SYS_RESOURCE":
        return "Allows to override disk quota limits. Allows to use reserved disk space.";
      case "SYSLOG":
        return "System log access";
      default:
        return "Unknown";
    }
  }
</script>

<svelte:options tag="addons-perm-list" />
<h4>Permissions</h4>
{#each perm as permission}
  <dl>
    <dt>{permission.label} ({permission.id})</dt>
    <dd>{permission.description}</dd>
  </dl>
{:else}
  <p>No permissions required</p>
{/each}
{#if addon.linux_capabilities}
  <h4>Linux capabilities</h4>
  {#each addon.linux_capabilities as cap}
    <dl>
      <dt>{cap}</dt>
      <dd>
        {@html cap_description(cap)}
      </dd>
    </dl>
  {/each}
{/if}
{#if addon.firewall_allow || addon.ports}
  <h4>Networking</h4>
  {#if addon.firewall_allow && addon.firewall_allow.includes('*')}
    <span class="text-danger">FULL Internet access!</span>
    . An Addon developer should usually restrict this to a set of required
    internet addresses!
  {:else if addon.firewall_allow}
    Allow Internet Access to
    <ul>
      {#each addon.firewall_allow as rule}
        <li>{rule}</li>
      {/each}
    </ul>
  {/if}
  {#if addon.ports}
    Expose ports to Internet:
    {#each addon.ports as rule}{rule};&nbsp;{/each}
  {/if}
{/if}
{#each installations as installment, installation_index}
  <div class="card m-3 p-3">
    <h5>{installment.title}</h5>
    {#if installment.thisaddon.s == 'downloading'}
      <button disabled class="btn btn-outline-info">
        Downloading Addon: {installment.thisaddon.d * 100}%
      </button>
    {:else if installment.thisaddon.s == 'installing'}
      <button disabled class="btn btn-outline-info">Installing &hellip;</button>
    {:else if installment.thisaddon.s == 'uninstalling'}
      <button disabled class="btn btn-outline-info">
        Uninstalling &hellip;
      </button>
    {:else if installment.thisaddon.s == 'installed'}
      <small class="mb-2">This Addon is already installed</small>
      <div>
        <button
          class="btn btn-outline-danger mr-3"
          disabled={queue[installment.id]}
          on:click={e => action(e, installment, installation_index, 'uninstall')}>
          Uninstall
        </button>
        <button
          class="btn btn-outline-warning"
          disabled={is_old_version(installment.thisaddon) || queue[installment.id]}
          on:click={e => action(e, installment, installation_index, 'update')}>
          Update {installment.thisaddon.v} to {addon.version}
        </button>
      </div>
    {:else if installment.thisaddon.s == 'error'}
      <p>An error occured: {installment.thisaddon.d}</p>
    {:else}
      <button
        class="btn btn-outline-success"
        disabled={queue[installment.id]}
        on:click={e => action(e, installment, installation_index, 'install')}>
        Install
      </button>
    {/if}
    {#if queue[installment.id]}
      {#if queue[installment.id].code == 'push'}
        <small>
          <b>Trasmit!</b>
          Transmitting command to backend.
        </small>
      {:else if queue[installment.id].code == 'queue'}
        <small>
          <b>Queued!</b>
          A queued Addon will be installed / uninstalled soon by the
          Addon-Manager.
        </small>
      {/if}
    {/if}
    {#if error_message}
      <small class="text-danger">{error_message}</small>
    {/if}
  </div>
{:else}
  <p>
    No Installations registered! Log into
    <b>Setup &amp; Maintenance</b>
    and click on "Register this installation on openhabx.com".
  </p>
{/each}
