<script>
  import { fade } from "svelte/transition";

  export let id="modal";
  export let key = "simple-modal";
  export let closeButton = true;
  export let closeOnEsc = true;
  export let closeOnOuterClick = true;
  export let transitionBg = fade;
  export let transitionBgProps = { duration: 250 };
  export let transitionWindow = transitionBg;
  export let transitionWindowProps = transitionBgProps;

  let Component = null;
  let props = null;
  let confirm_cb = (event)=>{};

  let background;
  let wrap;

  export const open = (NewComponent, newProps = {}, confirmCb=(event)=>{}) => {
    Component = NewComponent;
    props = newProps;
    confirm_cb = confirmCb;
  };

  export const close = () => {
    Component = null;
    props = null;
  };

  const handleKeyup = ({ key }) => {
    if (closeOnEsc && Component && key === "Escape") {
      event.preventDefault();
      close();
    }
  };

  const handleOuterClick = event => {
    if (
      closeOnOuterClick &&
      (event.target === background || event.target === wrap)
    ) {
      event.preventDefault();
      close();
    }
  };
</script>

<svelte:window on:keyup={handleKeyup} />
<svelte:options tag="ui-modal" />

<div class="modal_dialog" {id}>
  {#if Component}
    <div
      class="bg"
      on:click={handleOuterClick}
      bind:this={background}
      transition:transitionBg={transitionBgProps}>
      <div class="window-wrap" bind:this={wrap}>
        <div class="window" transition:transitionWindow={transitionWindowProps}>
          {#if closeButton}
            <button on:click={close} class="close" />
          {/if}
          <div class="content">
            <Component {...props} on:confirm={confirm_cb} />
          </div>
        </div>
      </div>
    </div>
  {/if}
</div>
