<script>
  import { onMount, setContext } from "svelte";
  import { permissions } from "./store_permissions.js";
  import { actionqueue } from "./addon_action_queue.js";
  import Modal from "./Modal.svelte";
  import InstallModal from "./install_modal.svelte";

  import Entry from "./entry.svelte";

  export let bindings = true;
  export let ioservices = true;
  export let ruletemplates = true;
  export let userdata = {};
  $: filters = {
    binding: bindings,
    ioservice: ioservices,
    ruletemplate: ruletemplates
  };
  export let searchstring = "";
  export let src = undefined;

  let database = undefined;
  let loading = false;
  let db = [];
  let db_searchstring = "";
  let error_message = "";

  let searchoverview;

  let modal;

  $: setContext("modal", modal);

  $: console.log("USERDATA CHANGED", userdata);

  function getMatches(searchstring) {
    if (searchstring === "") return [];

    let alreadyFound = {};
    let matches = [];
    try {
      for (let match of db_searchstring.matchAll(searchstring)) {
        const start = db_searchstring.lastIndexOf("{", match.index);
        if (alreadyFound[start]) continue;
        alreadyFound[start] = true;
        const end = db_searchstring.indexOf("}", match.index);
        matches.push(JSON.parse(db_searchstring.substring(start, end + 1)).id);
      }
      error_message = "";
      return matches;
    } catch (e) {
      error_message = e.message;
    }
    return [];
  }

  $: seachresults = getMatches(searchstring)
    .map(item => database.db_by_id[item])
    .filter(item => filters[item.type]);

  let recommended = [];
  let last_updated = [];
  let often_installed = [];

  function dbChanged(ev) {
    loading = !ev.detail.ok && !ev.detail.err;
    if (loading) return;
    db = ev.target.db;
    db_searchstring = database.searchstr;
    recommended = ev.target.recommended;
    $permissions = ev.target.permissions;
    last_updated = ev.target.last_updated.slice(0, 7);
    often_installed = ev.target.often_installed.slice(0, 3);
  }

  /**
   * @param[string] id The addon id
   */
  function setSearchTerm(id = "") {
    searchstring = id;
    searchoverview.dispatchEvent(
      new CustomEvent("input", {
        detail: id,
        bubbles: true,
        cancelable: true,
        composed: true
      })
    );
  }

  export function resetSearchTerm() {
    searchstring = "";
  }

  $: if (db && searchoverview) {
    let count = 0;
    for (let item of db) if (filters[item.type]) ++count;
    searchoverview.dispatchEvent(
      new CustomEvent("countedmessage", {
        detail: `Searching ${count} Addons`,
        bubbles: true,
        cancelable: true,
        composed: true
      })
    );
  }

  $: if (src) {
    database = document.getElementById(src);
    if (database) {
      if (database.db.length > 0) db = database.db;
      database.addEventListener("loader", dbChanged);
    }
  }

  function submit_rate(e) {
    searchoverview.dispatchEvent(
      new CustomEvent("rated", {
        detail: e.detail,
        bubbles: true,
        cancelable: true,
        composed: true
      })
    );
  }

  function install_addon(e) {
    searchoverview.dispatchEvent(
      new CustomEvent("addon_queue", {
        detail: Object.assign({ queue: actionqueue }, e.detail),
        bubbles: true,
        cancelable: true,
        composed: true
      })
    );
  }

  onMount(() => {
    return () => {
      if (database) database.removeEventListener("loader", dbChanged);
    };
  });
</script>

<link rel="stylesheet" href="/css/main.min.css" />
<link rel="stylesheet" href="/css/addons.min.css" />
<link rel="stylesheet" href="/css/fontawesome.min.css" />

<svelte:options tag="ui-addons-search" />

<Modal bind:this={modal} />

{#if loading}
  <h1>Loading...</h1>
{:else if searchstring.length === 0}
  <div class="searchoverview mt-4" bind:this={searchoverview}>
    <section>
      <h2>Often Installed</h2>
      {#each often_installed as addon}
        <Entry
          {userdata}
          {addon}
          on:rate={submit_rate}
          on:click={e => setSearchTerm(e.detail.id)} />
      {/each}
    </section>

    <section>
      <h2>Recommended</h2>
      {#each recommended as addon}
        <Entry
          {userdata}
          {addon}
          on:rate={submit_rate}
          on:click={e => setSearchTerm(e.detail.id)} />
      {/each}
    </section>

    <section>
      <h2>How To Publish</h2>
      <p>
        Learn how to write and publish your own Addon in the
        <a href="/developer/addons">Developer Section</a>
        . Publish Rule Templates via the
        <b>Setup &amp; Maintenance</b>
        interface.
      </p>
      <h2>Latest Updates</h2>
      <ul>
        {#each last_updated as addon}
          <li class="mb-2">
            {addon.label}
            {#if addon.changelog_url}
              <a href={addon.changelog_url}>Changelog</a>
            {/if}
            <br />
            Updated on {new Date(addon.last_updated).toLocaleString()} to
            version {addon.version}.
          </li>
        {/each}
      </ul>
    </section>
  </div>
{:else}
  <div class="searchoverview details container my-4" bind:this={searchoverview}>
    {#each seachresults as addon}
      <Entry
        fullrender="true"
        {userdata}
        {addon}
        on:rate={submit_rate}
        on:install={install_addon}
        on:click={e => setSearchTerm(e.detail.id)} />
    {:else}
      <p>
        No results found for "{searchstring}" :/
        <br />
        Did you know that you can use "." as search term to show all Addons? :)
      </p>
      {#if error_message}
        <div class="bs-callout bs-callout-warning">{error_message}</div>
      {/if}
    {/each}
  </div>
{/if}
