import { writable } from 'svelte/store';

export const actionqueue = writable({});

export function fill_queue(userdata) {
    if (!userdata.addonqueue) return;
    for (const item of Object.values(userdata.addonqueue)) {
        $actionqueue[item.install_id + "_" + item.addon_id] = value;
    }
}

export function queue_key(install_id, addon_id) {
    return install_id + "_" + addon_id;
}
