<script>
  import "./starrating.js";
  import InstallModal from "./install_modal.svelte";
  import NotLoggedIn from "./notloggedin.svelte";

  import { createEventDispatcher, getContext } from "svelte";
  const dispatch = createEventDispatcher();

  export let addon;
  export let userdata = {};
  export let fullrender = false;

  let modal = getContext("modal");

  $: item_rating = addon && addon.stat ? addon.stat.p / addon.stat.v : 0.0;
  $: user_item_rating =
    addon && userdata.ratings && userdata.ratings[addon.id]
      ? userdata.ratings[addon.id]
      : 0;

  $: logo_url = addon
    ? addon.logo_url ||
      (addon.github ? addon.github + "/raw/master/logo.png" : null)
    : null;

  $: suggest_logo_url = addon
    ? addon.homepage
      ? addon.homepage
      : addon.github
    : null;

  function submit_rate(e) {
    const target = e.target;
    target.disabled = true;
    dispatch("rate", {
      id: addon.id,
      rate: e.detail.rate,
      confirm: () => (target.disabled = false)
    });
  }

  function showPopup() {
    if (!userdata.user || !userdata.user.uid) modal.open(NotLoggedIn);
  }

  function install(e) {
    e.stopPropagation();
    modal.open(
      InstallModal,
      {
        userdata,
        addon
      },
      confirm_event => dispatch("install", confirm_event.detail)
    );
  }

  function maintenance_code_str(code) {
    switch (code) {
      case "AVAILABLE":
        return "Maintained &amp; Available";
      case "UNMAINTAINED":
        return "<span class='text-danger'>Unmaintained</span>";
      case "REPLACED":
        return "<span class='text-danger'>Replaced</span> by another Addon!";
      case "REMOVED":
        return "<span class='text-danger'>Removed</span>";
      default:
        return code;
    }
  }
</script>

<style>
  table.property-table th {
    width: 1px;
    white-space: nowrap;
  }
</style>

<svelte:options tag="addons-entry" />
{#if addon}
  <article
    id={addon.id}
    class="mb-3 {addon.status.code}"
    on:click={e => dispatch('click', { id: addon.id })}>
    <header title={addon.id}>
      <span>{addon.label}</span>
      <small class="ml-2">{addon.version}</small>
      <small class="" style="white-space: nowrap; text-overflow: ellipsis;">
        – By {addon.author}
      </small>
    </header>
    <section class="logo">
      {#if !logo_url}
        <img src={logo_url} alt="Logo" on:error={(logo_url = null)} />
      {:else}
        <div>
          {#if suggest_logo_url}
            <a href={suggest_logo_url}>Suggest Logo</a>
          {/if}
        </div>
      {/if}
    </section>
    <section class="description">
      <p>{addon.description}</p>
      {#if fullrender !== false}
        <table class="table table-hover table-sm property-table">
          <tr>
            <th scope="row" style="width: 1px;white-space: nowrap;">Updated</th>
            <td>
              {new Date(addon.last_updated).toLocaleString()}
              {#if addon.changelog_url}
                <a href={addon.changelog_url}>Changelog</a>
              {/if}
            </td>
          </tr>
          <tr>
            <th scope="row">Maintenance</th>
            <td>
              {@html maintenance_code_str(addon.status.code)}
            </td>
          </tr>
          <tr>
            <th scope="row">Install Size</th>
            <td title={addon.size + ' bytes'}>
              {Number.parseFloat(addon.size / 1024 / 1024).toFixed(2)} MB
            </td>
          </tr>
          <tr>
            <th
              scope="row"
              title="Estimated Memory Usage. This is an Addon developer provided
              value.">
              Est. Mem Usage
            </th>
            <td>{Number.parseFloat(addon.estimated_mem.min).toFixed(2)} - {Number.parseFloat(addon.estimated_mem.max).toFixed(2)} MB</td>
          </tr>
          <tr>
            <th scope="row">Reviewed</th>
            <td>{addon.reviewed_by.length > 0 ? 'Yes' : 'No'}</td>
          </tr>
          {#if addon.stat && addon.stat.license && addon.stat.license.name}
            <tr>
              <th scope="row">License</th>
              <td>{addon.stat.license.name}</td>
            </tr>
          {/if}
        </table>
        <button
          class="btn btn-outline-success"
          disabled={!userdata.user || !userdata.user.uid}
          title={userdata.user && userdata.user.uid ? '' : 'You need to login!'}
          on:click={install}
          on:install>
          Check Permissions &amp; Install
        </button>
      {/if}
    </section>
    <footer>
      {#if addon.stat && addon.stat.stars}
        <a
          class="mr-2 noref"
          href={addon.github}
          target="_blank"
          title={addon.github}>
          <span class="mr-2">
            <i class="fas fa-star" />
            {addon.stat.stars} Stars
          </span>
          <span class="mr-2">
            <i class="fas fa-info-circle" />
            {addon.stat.issues} Issues
          </span>
        </a>
      {/if}
      {#if addon.stat}
        <span class="mr-2">
          <i class="fas fa-download" />
          {addon.stat.d} Downloads
        </span>
        <ui-star-rating
          class="ml-auto"
          title={userdata.user && userdata.user.uid ? 'Rate this Addon' : 'You need to login to rate'}
          value={item_rating}
          disabled={!userdata.user || !userdata.user.uid}
          highlight={user_item_rating}
          on:rate={submit_rate}
          on:invalid={showPopup} />
      {/if}
    </footer>
  </article>
{/if}
